.skills-mobile {
    background: $dark;
    display: none;

    .master-image {
        background: $light;
        height: $masterHeight;
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        margin-bottom: 0.2em;
    }

    .master-level {
        background: $light;
        height: $masterHeight;

        .mobile-fill {
            height: $masterHeight;
            width: 0;
            background: $accent;
            border-right: 5px solid $dark;
        }
    }

    .mobile-desc {
        opacity: 0.9;

        h1 {
            color: $light;
            font-size: 5vmin;
            font-family: $bBook;
            text-align: center;
            z-index: 1;
        }
    }

    #html-mobile {
        .mobile-image {
            @extend .master-image;
            background-image: url($imageHtml);
        }

        .mobile-level {
            @extend .master-level;
        }
    }

    #sass-mobile {
